import { useState } from "react";
import { IconButton, Snackbar } from "@mui/material";
import SvgIcon from '@mui/material/SvgIcon';

const CopyToClipboardButton = ({ onCopy }) => {
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(true);
    onCopy();
  };

  return (
    <>
      <IconButton onClick={handleClick} color="primary">
        <CopyIcon sx={{ fontSize: 16 }}/>
      </IconButton>
      <Snackbar
        message="Copied to clibboard"
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        autoHideDuration={2000}
        onClose={() => setOpen(false)}
        open={open}
      />
    </>
  );
};

export default CopyToClipboardButton;

function CopyIcon(props) {
  return (
    <SvgIcon {...props}>
      <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 0 24 24" width="24px" fill="#e3f2fd"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M16 1H4c-1.1 0-2 .9-2 2v14h2V3h12V1zm3 4H8c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h11c1.1 0 2-.9 2-2V7c0-1.1-.9-2-2-2zm0 16H8V7h11v14z"/></svg>
    </SvgIcon>
  );
}