import Checkbox from '@mui/material/Checkbox';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormGroup from '@mui/material/FormGroup';

export default function ServiceDetails({ services, updateParent }) {

  const serviceItems = services.map(service =>
      <FormControlLabel
        label={service.Item +  ' -  ' + service.DisplayPrice}
        control={
          <Checkbox
            onChange={(event) => updateParent(service, event.target.checked)}
          />
        }
      />
    );

  return (
    <FormGroup>
      {serviceItems}
    </FormGroup>
  );
}