import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';

import ServiceDetails from '../service-details/service-details.js';

export default function ServiceDrawer({ pricingSheets, updateParent }) {
  const drawers = pricingSheets?.map(drawer =>
    <Accordion>
      <AccordionSummary id="panel-header" aria-controls="panel-content">
        {drawer.title}
      </AccordionSummary>
      <AccordionDetails>
        <ServiceDetails services={drawer.services} updateParent={updateParent} />
      </AccordionDetails>
    </Accordion>
  );

  return (
    <div>
      {drawers}
    </div>
  );
}