import TextField from '@mui/material/TextField';
import TextareaCopyButton from '../text-area-copy-button/text-area-copy-button.js';
import Box from '@mui/material/Box';
import { useState, useEffect } from "react";

import styles from './posting-space.module.css';

export default function PostingSpace({ selectedServices }) {
  const [postingInfo, setPostingInfo] = useState({
    ticketNumber: '',
    additionalInfo: '',
    posts: [],
  });

  useEffect(() => {
    formatPostsForSelectedServices()
  }, [selectedServices, postingInfo.ticketNumber, postingInfo.additionalInfo]);

  function formatPostsForSelectedServices () {
    var posts = []
    for (let i = 0; i < selectedServices.length; i++) {
      // If list contains object where key === selectedServices[i].Roles, add new line to that post
      // Else, create a new post where key is the role and value is the first line + the second line
      const selectedService = selectedServices[i];
      var index = posts.findIndex(p => p.role === selectedService.Roles)
      const quoting = selectedService.DisplayPrice === 'Quote';
      if (index === -1) {
        var newPostText = `<@&${selectedService.Roles}> #ticket-${postingInfo.ticketNumber} / ${postingInfo.ticketNumber}\n${selectedService.Item}`;
        if (!selectedService.quoting) {
          newPostText = newPostText + ` - ${selectedService.DisplayPrice}`;
        }
        const newPostObject = {
          'role': selectedService.Roles,
          'text': newPostText,
          'quoting': quoting,
        }
        posts.push(newPostObject);
      } else {
        var existingPost = posts[index]
        newPostText = `${existingPost.text}\n${selectedService.Item}`;
        if (!selectedService.quoting) {
          newPostText = newPostText + ` - ${selectedService.DisplayPrice}`;
        }
        const updatedPost = {
          'role': selectedService.Roles,
          'text': newPostText,
          'quoting': quoting,
        }
        posts[index] = updatedPost;
      }
    }

    // Add additional info for each post
    for (let i = 0; i < posts.length; i++) {
      if (postingInfo.additionalInfo) {
        posts[i].text = `${posts[i].text}\n${postingInfo.additionalInfo}`;
      }
      if (posts[i].quoting) {
        posts[i].text = `${posts[i].text}\n**Quote - ** #quoting `;
      }
    }

    setPostingInfo({
      ticketNumber: postingInfo.ticketNumber,
      additionalInfo: postingInfo.additionalInfo,
      posts: posts,
    });
  }

  function handleTicketNumberChange (event) {
    setPostingInfo({
      ticketNumber: event.target.value,
      additionalInfo: postingInfo.additionalInfo,
      posts: postingInfo.posts,
    });
  }

  function handleAdditionalInfoChange (event) {
    setPostingInfo({
      ticketNumber: postingInfo.ticketNumber,
      additionalInfo: event.target.value,
      posts: postingInfo.posts,
    });
  }

  const postings = postingInfo.posts.map(post =>
    <TextareaCopyButton
      editable={false}
      value={post.text}
    />
  );

  return (
    <Box
      component="form"
      sx={{
        '& .MuiTextField-root': { m: 1, width: '35rem' },
      }}
      noValidate
      autoComplete="off"
    >
      <div>
        <TextField
          multiline
          required
          label="Ticket Number"
          id="outlined-required"
          value={postingInfo.ticketNumber}
          onChange={(event) =>
            handleTicketNumberChange(event)
          }
        />
      </div>
      <TextField
        multiline
        label="Additional Information"
        id="outlined-required"
        value={postingInfo.additionalInfo}
        onChange={(event) =>
          handleAdditionalInfoChange(event)
        }
        minRows={3}
      />
      <div className={styles.postings}>
        {postings}
      </div>
    </Box>
  );
}