import * as React from 'react';
import TextField from '@mui/material/TextField';
import CopyToClipboardButton from '../copy-to-clipboard-button/copy-to-clipboard-button.js';

import styles from './text-area-copy-button.module.css';

export default function TextAreaCopyButton({ value }) {

  function copyToClipboard () {
    navigator.clipboard.writeText(value);
  }

  return (
    <div className={styles.relativeContainer}>
      <div className={styles.copyButton}>
        <CopyToClipboardButton onCopy={copyToClipboard}/>
      </div>
      <TextField
        multiline
        id="outlined-required"
        value={value}
        minRows={3}
        readOnly
      />
    </div>
  );
}